<!--eslint-disable-->
<template>
  <div class="testemunhos-wrapper">
    <div id="testemunhos" class="movement">
      <div class="title main">O que dizem sobre n√≥s</div>

      <div class="line"></div>

      <div class="slider-wrapper">
        <div class="slider">
          <div
            class="slide"
            :class="{'active': isActive}"
            v-for="t in testemunhos"
            :key="t.id"
            v-html="t.text"
            :data-slide-id="t.id"
          ></div>
        </div>
        <div class="indicators">
          <div
            class="ind"
            :class="{'active': isActive}"
            v-for="t in testemunhos"
            :key="t.id"
            @click="currentSlide(t.id)"
          ></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  data() {
    return {
      isActive: true,
      index: 1,
      testemunhos: [
        {
          id: 1,
          text:
            "Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dovlore.",
        },
        {
          id: 2,
          text: "Another Sample text 2",
        },
        {
          id: 3,
          text: "Another Sample text 3",
        },
        {
          id: 4,
          text: "Another Sample text 4",
        },
      ],
    };
  },
  methods: {
    showTestemunho(n) {
      var i;
      var slides = document.querySelectorAll(".slider .slide");
      var dots = document.querySelectorAll(".indicators .ind");
      if (n > slides.length) {
        this.index = 1;
      }
      if (n < 1) {
        this.index = slides.length;
      }
      for (i = 0; i < slides.length; i++) {
        slides[i].className = "slide";
      }
      for (i = 0; i < dots.length; i++) {
        dots[i].className = dots[i].className.replace(" active", "");
        this.isActive = false;
      }
      slides[this.index - 1].className = "slide active";
      dots[this.index - 1].className += " active";
      this.isActive = true;
    },
    prevSlides(n) {
      this.showTestemunho((this.index -= n));
    },
    plusSlides(n) {
      this.showTestemunho((this.index += n));
    },
    currentSlide(n) {
      this.showTestemunho((this.index = n));
    },
  },
  mounted() {
    var vm = this;
    this.showTestemunho(this.index);

    var slider = document.querySelector(".slider");
    var slides = document.querySelectorAll(".slider .slide");

    if (window.innerWidth < 600) {
      slider.addEventListener("touchstart", handleTouchStart, false);
      slider.addEventListener("touchmove", handleTouchMove, false);

      var xDown = null;
      var yDown = null;

      function getTouches(evt) {
        return evt.touches || evt.originalEvent.touches;
      }

      function handleTouchStart(evt) {
        const firstTouch = getTouches(evt)[0];
        xDown = firstTouch.clientX;
        yDown = firstTouch.clientY;
      }

      function handleTouchMove(evt) {
        if (!xDown || !yDown) {
          return;
        }

        var xUp = evt.touches[0].clientX;
        var yUp = evt.touches[0].clientY;

        var xDiff = xDown - xUp;
        var yDiff = yDown - yUp;

        var active = document.querySelector(".slider .slide.active");
        var id = active.getAttribute("data-slide-id");

        if (Math.abs(xDiff) > Math.abs(yDiff)) {
          /*most significant*/
          if (xDiff > 0) {
            /* left swipe */
            vm.plusSlides(1);
          } else {
            /* right swipe */
            vm.prevSlides(1);
          }
        } // else {
        // if (yDiff > 0) {
        //   /* up swipe */
        // } else {
        //   /* down swipe */
        // }
        // }
        /* reset values */
        xDown = null;
        yDown = null;
      }
    }
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(130);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.testemunhos-wrapper {
  background-color: $lt-gray;
  height: pxToEm(530);
  padding: $pd-desktop;
  display: flex;
  align-items: center;
  justify-content: center;

  @media only screen and (min-width: 1200px) and (max-width: 1919px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    height: auto;
    padding: 0 pxToEm(20);
  }

  #testemunhos {
    height: 65%;
    width: 100%;
    display: flex;
    flex-direction: column;
    margin: $mg-desktop;
    align-items: center;
    justify-content: space-between;

    @media only screen and (min-width: 300px) and (max-width: 599px) {
      height: pxToEm(300);
      justify-content: center;
      margin: 0 pxToEm(20);
    }

    .title {
      color: $dk-gray-3;
      margin-top: pxToEm(60);

      @media only screen and (min-width: 300px) and (max-width: 599px) {
        margin-top: pxToEm(10);
      }
    }

    .line {
      height: pxToEm(1);
      width: pxToEm(80);
      background-color: $turquiose;

      @media only screen and (min-width: 300px) and (max-width: 599px) {
        margin: pxToEm(10) 0;
      }
    }

    .slider-wrapper {
      max-width: pxToEm(960);
      width: 100%;
      max-height: pxToEm(120);
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: pxToEm(60);

      @media only screen and (min-width: 300px) and (max-width: 599px) {
        height: auto;
        max-height: 100%;
        margin-bottom: pxToEm(10);
        justify-content: center;
        align-items: center;
      }

      .slider {
        width: 100%;
        height: 100%;

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          height: pxToEm(120);
          margin-bottom: pxToEm(10);
        }

        .slide {
          text-align: center;
          display: inline-block;
          position: absolute;
          max-width: pxToEm(960);
          width: 100%;
          color: $dk-gray-3;
          font-family: "OS-Regular";
          opacity: 0;
          transition: 0.5s;

          &.active {
            opacity: 1;
          }
        }
      }

      .indicators {
        display: flex;
        width: pxToEm(130);
        justify-content: space-between;

        .ind {
          border-radius: $circle;
          width: pxToEm(22);
          height: pxToEm(22);
          background-color: $white;
          cursor: pointer;

          &.active {
            background-color: $turquiose;
          }
        }
      }
    }
  }
}
</style>



